<template>
  <div>
    <div class="header">
      <span class="iconfont icon-jiantou" @click="onClickLeft"></span>
    </div>
    <div class="top-bg">
      <h3>账单详情</h3>
    </div>
    <div class="overview clearfix">
      <div class="fl">
        <h5 class="type">会员充值</h5>
        <p class="time">{{ data.createTime }}</p>
      </div>
      <div class="fr">
        <span class="num">￥{{ data.rechargeMoney }}</span>
      </div>
    </div>

    <p class="ps"><span class="iconfont icon-tishi"></span> 信息由{{data.createBy}}录入</p>

    <div class="detail-info">
      <dl class="clearfix">
        <dt class="fl">订单号</dt>
        <dd>{{ data.orderNo }}</dd>
      </dl>

      <dl class="clearfix">
        <dt class="fl">会员姓名</dt>
        <dd>{{ data.memberName }}</dd>
      </dl>

      <dl class="clearfix">
        <dt class="fl">会员卡号</dt>
        <dd>{{ data.memberNo }}</dd>
      </dl>

      <dl class="clearfix">
        <dt class="fl">付款方式</dt>
        <dd>{{ paytypeMap[data.payType] }}</dd>
      </dl>

      <dl class="clearfix">
        <dt class="fl">赠送金额</dt>
        <dd>{{ data.extraMoney || 0 }}元</dd>
      </dl>

      <dl class="clearfix">
        <dt class="fl">备注</dt>
        <dd>{{ data.remark }}</dd>
      </dl>
    </div>
  </div>
</template>

<script>
import { rechargeOneHistory } from '@/api/index';
// eslint-disable-next-line no-unused-vars
import { mapState, mapGetters } from 'vuex';

export default {
  data() {
    return {
      data: {}
    };
  },
  computed: {
    ...mapGetters(['paytypeMap'])
  },
  created() {
    rechargeOneHistory(this.$route.query.id).then(res => {
      if (res) {
        if (res.data.code === 0) {
          this.data = res.data.data;
        } else {
          this.$Toast(res.data.msg);
        }
      }
    });
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>

